import React ,{ useEffect , useState } from 'react'
import { Chart } from 'react-charts'
import Button from '@material-ui/core/Button'
import ArrowBackIcon from '@material-ui/icons/ArrowBack'
import { useHistory } from "react-router-dom"

export default function Reports({vacArr }) {
    
    const history = useHistory()

    const reported =[]
    const [rep, setRep] = useState([])


    useEffect(() => {

        if (!localStorage.token) 
            {history.push('/login')}

        (async () =>{
                
            const followed = vacArr.filter(vac => vac.followers>0)

            followed.forEach(vac => {
                reported.push({
                    x:vac.destenation,
                    y:vac.followers
                })
            })
            console.log(reported)
            setRep(reported)


        })()
    }, [])

   
    const data = [
                    {
                    label: 'Series 1',
                    data: rep  
                    }
                  ]        
          
    const series = {
              type: 'bar',
              xName: 'xx', 
              yName: 'yy',
            }   
            
    const axes =  [
                { primary: true, type: 'ordinal', position: 'bottom',  },
                {  position: 'left', type: 'linear' , base:0 , tickPadding:5}
            ]
    
            
    return (
            <div>

                <Button style={{  marginLeft:'90%' ,marginTop:'20px'}}
                        color="inherit" type="button"
                        onClick={()=>history.push('/home')}>
                        <ArrowBackIcon/>BACK
                </Button>

                <h1 style={{ marginLeft:'40%' }}>Followed vacations</h1>
   
    
                <div style={{ width: '1000px', height: '600px' , margin:'60px auto' ,borderRadius: '5px'}}>

                                    <Chart data={data} axes={axes} series={series}/>

            
                </div>      

            </div>
            )
}
