import React , { useState } from 'react'
import Avatar from '@material-ui/core/Avatar'
import Button from '@material-ui/core/Button'
import CssBaseline from '@material-ui/core/CssBaseline'
import TextField from '@material-ui/core/TextField'
import Link from '@material-ui/core/Link'
import Grid from '@material-ui/core/Grid'
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography'
import { makeStyles } from '@material-ui/core/styles'
import Container from '@material-ui/core/Container'


const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  }
}))

export default function Login({history}) {

    const classes = useStyles()

    const [u_name, setU_name] = useState("")
    const [password, setPassword] = useState("")


  return (
    <Container component="main" maxWidth="xs">

      <CssBaseline />

      <div className={classes.paper}>

            <Avatar className={classes.avatar}>
              <LockOutlinedIcon />
            </Avatar>

            <Typography component="h1" variant="h5">
              Sign in
            </Typography>

        <form className={classes.form} noValidate>

            <TextField
              variant="outlined" margin="normal" required fullWidth id="username" label="username"
              name="username" autoComplete="username" autoFocus onChange={e=>{setU_name(e.target.value)}}
            />
            
            <TextField
              variant="outlined" margin="normal" required fullWidth name="password" label="Password"
              type="password" id="password" autoComplete="current-password" onChange={e=>{setPassword(e.target.value)}}
            />

              <Button
                  type="button" fullWidth variant="contained" color="primary" className={classes.submit}

                  onClick={async ()=>{

                      try {
                        // console.log(u_name,password)
                          const res = await fetch('http://localhost:1000/auth/login', {
                              method:"post",
                              headers:{"content-type":"application/json"},
                              body: JSON.stringify({u_name, password})
                          })

                          console.log(res)
                          const data = await res.json()
                          if (res.status===400)
                          {alert(data.err)}
        
                          if(data.token){
                            localStorage.token = data.token
                            history.push('/home')
                          }
                      } catch (err) {
                          console.log(err)
                      }
                  }}>Sign In
            </Button>

            <Grid container>
                <Grid item>

                  <Link variant="body2" onClick={()=>{history.push('/register')}}>
                    {"Don't have an account? Sign Up"}
                  </Link>

                </Grid>
            </Grid>

        </form>

      </div>

    </Container>
  )
}