import React , { useState } from 'react'
import Avatar from '@material-ui/core/Avatar'
import Button from '@material-ui/core/Button'
import CssBaseline from '@material-ui/core/CssBaseline'
import TextField from '@material-ui/core/TextField'
import Grid from '@material-ui/core/Grid'
import LockOutlinedIcon from '@material-ui/icons/LockOutlined'
import Typography from '@material-ui/core/Typography'
import { makeStyles } from '@material-ui/core/styles'
import Container from '@material-ui/core/Container'
import Link from '@material-ui/core/Link'



const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function Register({history}) {

    const classes = useStyles()

    const [u_name, setU_name] = useState("")
    const [password, setPassword] = useState("")
    const [f_name, setF_name] = useState("")
    const [l_name, setL_name] = useState("")

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign up
        </Typography>
        <form className={classes.form} noValidate>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <TextField
                autoComplete="fname" name="firstName" variant="outlined" required fullWidth id="firstName"
                label="First Name" autoFocus onChange={e=>{setF_name(e.target.value)}}/>

            </Grid>

                <Grid item xs={12} sm={6}>
                    <TextField
                      variant="outlined" required fullWidth id="lastName" label="Last Name" name="lastName"
                      autoComplete="lastname" onChange={e=>{setL_name(e.target.value)}}/>
                </Grid>
                
                <Grid item xs={12}>
                    <TextField
                      variant="outlined" required fullWidth id="username" label="username" name="username"
                      autoComplete="username" onChange={e=>{setU_name(e.target.value)}}/>
                </Grid>

                <Grid item xs={12}>
                    <TextField
                      variant="outlined" required fullWidth name="password" label="Password" type="password"
                      id="password" autoComplete="current-password" onChange={e=>{setPassword(e.target.value)}}/>
                </Grid>
                
          </Grid>

          <Button
              type="button" fullWidth variant="contained" color="primary" className={classes.submit}

              onClick={async ()=>{
                  try {
                    
                    const res = await fetch('http://localhost:1000/auth/register', {
                      method:"post",
                      headers:{"content-type":"application/json"},
                      body: JSON.stringify({u_name, password, f_name, l_name})
                    })

                    const data = await res.json()
                    if (res.status===400)
                    {alert(data.err)}

                        if(res){
                          history.push('/login')
                        }
                    } catch (err) {
                        console.log(err)
                    }
                }}
              >Sign Up
          </Button>

          <Grid container justify="flex-end">

              <Grid item  >

                <Link variant="body2" onClick={()=>{history.push('/login')}}>
                  Already have an account? Sign in
                </Link>

              </Grid>

          </Grid>
        </form>
      </div>
    </Container>
  )
}