export const userIdReducer = (state =0, action) => {
    switch (action.type) {
        case "SET_USER":
            state =  action.id
            return  state
        default:
            return state 
        }
}


export const userRoleReducer = (state = 0, action) => {
    switch (action.type) {
        case "SET_USER":
            state =  action.role
            return  state
        default:
            return state 
        }
}

export const userNameReducer = (state = '', action) => {
    switch (action.type) {
        case "SET_USER":
            state =  action.user
            return  state
        default:
            return state 
        }
}