import React , { useEffect, useState } from 'react'
import Header from './Header'
import Main from './Main'
import { useDispatch } from 'react-redux'


export default function Home() {

     const [userName , setUserName] = useState('')
     
     const dispatch = useDispatch()


    useEffect(() => {
        (async () =>{
            try {
                const id =  JSON.parse(atob(localStorage.token.split('.')[1])).id
                const role =  JSON.parse(atob(localStorage.token.split('.')[1])).role
                const user = JSON.parse(atob(localStorage.token.split('.')[1])).username

                setUserName(user)

                dispatch({type:"SET_USER", id, role , user })

            } catch (err) {
                console.log(err)
            }
           })()
        }, [])
   


    return (
              
            <div className='home'>
                <div className='header'>
                    <Header userName={userName}/>
                </div>

                <div className='list'>
                    <Main/>
                </div>
            </div>
        
    )
}

