import React from 'react'
import  VacItem from './VacItem'
import { useSelector } from 'react-redux'
import AddIcon from '@material-ui/icons/Add'
import IconButton from '@material-ui/core/IconButton'
import ShowChartIcon from '@material-ui/icons/ShowChart'
import { useHistory } from "react-router-dom"
import Tooltip from '@material-ui/core/Tooltip'


export default function VacList({vacArr , remove ,  handleClickOpen , follow  }) {

    const role =  useSelector(state=>state.userRole)

      const history = useHistory()

    return (

            <div>
                {role?
                    
                    <span className='admin' >
                            <h1 className='title'> So where should we take them next?</h1> 

                            <span >
                            <Tooltip title="ADD VACATION">
                                <IconButton  type='button' size="large" color="disabled" style={{  marginright:'3%' }}
                                onClick={()=>handleClickOpen(0)}> <AddIcon /> </IconButton>
                            </Tooltip>

                            <Tooltip title="SHOW REPORTS">
                                <IconButton  type='button' color="disabled" size="large" aria-label="reports" label="reports"  
                                onClick={()=>history.push('/home/reports')}> <ShowChartIcon />  </IconButton>
                            </Tooltip>
                            </span>    

                    </span>:

                <   h1 className='title'> So where do we go next...
                </h1>}

                {vacArr.map( vac => (
                <VacItem key={vac.v_id} vac={vac} remove={remove} follow={follow} handleClickOpen={handleClickOpen} />
                ))}

            </div>
      
    )
}

