import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import CardMedia from '@material-ui/core/CardMedia'
import IconButton from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'
import FavoriteIcon from '@material-ui/icons/Favorite'
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder'
import DeleteIcon from '@material-ui/icons/Delete'
import EditIcon from '@material-ui/icons/Edit'
import { useSelector } from 'react-redux'
import Tooltip from '@material-ui/core/Tooltip'


const useStyles = makeStyles({
    root: {
      maxWidth: 1500,
      margin: 'auto' ,
      marginTop: 80
    },
    media: {
      height: 350,
      
    },
})

export default function VacItem({ vac , remove , handleClickOpen , follow }) {

    const user =  useSelector(state=>state.userId)
    const role =  useSelector(state=>state.userRole)

    const classes = useStyles()

    return (
        <div>

        <Card className={classes.root}>
                <CardMedia
                  className={classes.media}
                  image={vac.image}
                  title={vac.destenation}
                />
                <CardContent>
                    <Typography gutterBottom variant="h5" component="h2">
                      
                                {vac.destenation} ${vac.price}

                        {role?
                        <span>

                            <Tooltip title="DELETE VACATION">
                              <IconButton type='button' size="small" color="primary"> <DeleteIcon 
                                  onClick={()=>remove(vac.v_id)}/>
                              </IconButton>
                            </Tooltip>

                            <Tooltip title="EDIT VACATION">
                              <IconButton type='button' size="small" color="primary"><EditIcon 
                                  onClick={()=>handleClickOpen(vac.v_id)}/></IconButton>
                              </Tooltip>

                            </span>:<span>
                              <IconButton type='button' size="small" color="primary" 
                                      onClick={()=>follow(vac.v_id,vac.u_id,vac.f_id,user)} > 
                                      {vac.u_id?
                                              <Tooltip title="UNFOLLOW"><FavoriteIcon /></Tooltip>:
                                              <Tooltip title="FOLLOW"><FavoriteBorderIcon/></Tooltip>} 
                              </IconButton>
                            </span>}

                              <Tooltip title="FOLLOWERS">
                                  <span className='fol'>{vac.followers }</span>
                              </Tooltip>

                    </Typography>

                    <Typography variant="body2" color="textSecondary" component="p">
                    {vac.description}
                    </Typography>

                    <Typography variant="h6"  component="p">
                    {vac.s_date.replaceAll("-","/").substr(0,10)} - {vac.e_date.replaceAll("-","/").substr(0,10)}
                    </Typography>

                </CardContent>
            </Card>

        </div>
    )
}
