import React , { useEffect,  useState } from 'react'
import Reports from './Reports'
import VacList from './VacList'
import Button from '@material-ui/core/Button'
import TextField from '@material-ui/core/TextField'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogTitle from '@material-ui/core/DialogTitle'
import { useHistory } from "react-router-dom"
import { BrowserRouter as Router, Redirect, Route, Switch} from "react-router-dom"


export default function Main() {

  const history = useHistory()
  const currentUser =  JSON.parse(atob(localStorage.token.split('.')[1])).id


  const [open, setOpen] = useState(false)
  const [vacArr , setVacArr] = useState([])
  const [update , setUpdate] = useState(true)
  const [destenation , setDestenation] = useState('')
  const [description , setDescription] = useState('')
  const [s_date , setS_date] = useState(new Date())
  const [e_date , setE_date] = useState(new Date())
  const [image , setImage] = useState('')
  const [price , setPrice] = useState('')
  const [old , setOld] = useState(0)



  function getFormattedDate(date) {

    let year = date.getFullYear()
    let month = (1 + date.getMonth()).toString().padStart(2, '0')
    let day = date.getDate().toString().padStart(2, '0')
  
    return year + '-' + month + '-' + day
}

  
  const handleClickOpen = async (v_id) => {

    const now = getFormattedDate(new Date())
    

    if(v_id){  

        const va = vacArr.find((vac) => vac.v_id === v_id)

        const start = new Date(va.s_date)
        const end = new Date(va.e_date)

        setOld(v_id)
        setDestenation(va.destenation)
        setDescription(va.description)
        setS_date(getFormattedDate(start))
        setE_date(getFormattedDate(end))
        setPrice(va.price)
        setImage(va.image)

      } else {

        setOld(0)
        setDestenation('')
        setDescription('')
        setPrice('')
        setImage('')
        setS_date(now)
        setE_date(now)
    }
      
    setOpen(true)

  }

  const handleClose = () => {
    setOpen(false)
  }
 
    useEffect(() => {

        if (!localStorage.token) 
            {history.push('/login')}

        (async () =>{
            try {
                
                const res = await fetch('http://localhost:1000/user', {
                    method:"get",   
                    headers: {"authorization": localStorage.token}
                })

                const data = await res.json()

                const followed = data.filter(fv => fv.u_id===currentUser).sort((a,b) =>{
                    const c = new Date(a.s_date)
                    const d = new Date(b.s_date)
                    return c-d
                })


                const unfollowed = data.filter(fv => fv.u_id!==currentUser).sort((a,b) =>{
                    const c = new Date(a.s_date)
                    const d = new Date(b.s_date)
                    return c-d
                })


            setVacArr(followed.concat(unfollowed))
            
            
        } catch (err) {
            console.log(err)
         }
        })()
     }, [update])


     const follow = async ( v_id , u_id ,f_id , user) => {

        try {

            let res

            if(u_id){

             res = await fetch('http://localhost:1000/user', {
             method:"delete", 
             body:JSON.stringify({
                v_id:v_id,
                f_id:f_id
               }),    
             headers: {
                "content-type":"application/json",
                "authorization": localStorage.token
                    }
                })

            } else {

             res = await fetch('http://localhost:1000/user', {
             method:"put", 
             body:JSON.stringify({
                 v_id:v_id,
                 u_id:user
                }),  
             headers: {
                "content-type":"application/json",
                "authorization": localStorage.token  
                }
                
            })
    }
        
        setUpdate(!update)

        console.log(res)
        
    } catch (err) {
        console.log('BLAH' ,err)
    }
}


     const remove = async (v_id) => {
        try {
            const res = await fetch('http://localhost:1000/admin', {
             method:"delete", 
             body:JSON.stringify({v_id:v_id}),  
             headers: {
                "content-type":"application/json",
                "authorization": localStorage.token
            }
        })
        
        setUpdate(!update)

        console.log(res)
        
    } catch (err) {
        console.log(err)
    }
}


const edit = async (old,destenation,description,price,s_date,e_date,image) => {

    try {
            console.log('edit')
            console.log(s_date)
            const res = await fetch('http://localhost:1000/admin', {
             method:"put", 
             body:JSON.stringify({
                 v_id:old,
                 destenation:destenation,
                 description:description,
                 price:price,
                 s_date:s_date,
                 e_date:e_date,
                 image:image
            }),  
             headers: {
                "content-type":"application/json",
                "authorization": localStorage.token
                 }
             })
             
             setUpdate(!update)

             console.log(res)
             
             const data = await res.json()
                          if (res.status===400)
                          {alert(data.err)}
            
        } catch (err) {
            console.log(err)
        }
    }


    const add = async (destenation,description,price,s_date,e_date,image) => {
        try {
            console.log('add')
            const res = await fetch('http://localhost:1000/admin', {
             method:"post", 
             body:JSON.stringify({
                destenation:destenation,
                description:description,
                price:price,
                s_date:s_date,
                e_date:e_date,
                image:image
           }),    
             headers: {
                "content-type":"application/json",
                "authorization": localStorage.token
                 }
             })
             
             setUpdate(!update)

             console.log(res)
             const data = await res.json()
                          if (res.status===400)
                          {alert(data.err)}
            
        } catch (err) {
            console.log(err)
        }
    }

    


    return (

        <Router>
            <div>
                <Switch>
                    <Route path="/home/reports" component={()=><Reports vacArr={vacArr}/>} />
                    <Route path="/home/list" component={()=><VacList vacArr={vacArr} 
                    remove={remove} follow={follow} handleClickOpen={handleClickOpen} />} />
                    <Redirect  from="/home" to="/home/list" exact />
                </Switch>

                <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">

                    <DialogTitle id="form-dialog-title" >{!old? <span>ADD VACATION</span>:<span>EDIT VACATION</span>}</DialogTitle>

                    <DialogContent>

                        <TextField autoFocus margin="dense" id="Destenation" type="text" fullWidth label="Destenation"
                            value={destenation} onChange={e=>{setDestenation(e.target.value)}} />

                        <TextField autoFocus margin="dense" id="Description" type="text" fullWidth label="Description"
                            value={description} onChange={e=>{setDescription(e.target.value)}} />

                        <TextField autoFocus margin="dense" id="Price" type="number" fullWidth label="Price - USD"
                            value={price} onChange={e=>{setPrice(e.target.value)}} />

                        <TextField autoFocus margin="dense" id="s_date" type="date" fullWidth label="Start date" 
                            value={s_date}    onChange={e=>{setS_date(e.target.value)}} />

                        <TextField autoFocus margin="dense" id="e_date" type="date" fullWidth label="End date"
                            value={e_date}  onChange={e=>{setE_date(e.target.value)}} />

                        <TextField autoFocus margin="dense" id="image" type="text" fullWidth label="image"
                            value={image}  onChange={e=>{setImage(e.target.value)}} />

                        <img src={image} alt={destenation} height="300"/>

                    </DialogContent>

                    <DialogActions>

                            <Button type='button'  color="primary" onClick={handleClose}>
                                Cancel
                            </Button>

                            <Button type='button'  color="primary" onClick={handleClose}>

                                {!old? <span onClick={()=>{
                                    if (s_date>e_date){
                                        setE_date(s_date)
                                        alert('set new end date')
                                    }  else {
                                        add(destenation,description,price,s_date,e_date,image)
                                    }
                                    }}>ADD TO DATABASE
                                    </span>:<span onClick={()=>{
                                    if (s_date>e_date){
                                        setE_date(s_date)
                                        alert('set new end date')
                                    }  else {
                                        edit(old,destenation,description,price,s_date,e_date,image)
                                    }
                                    }}>SAVE</span>}
                            </Button>

                    </DialogActions>
            </Dialog>
                
            </div>
        </Router> 
    )
}
